.key {
	min-width: fit-content;
	/* padding: 5px; */
	width: 3rem;
	background-color: #787c7e;
	font-weight: bold;
	font-size: 3rem;
	color: white;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	margin: 4px;
	cursor: pointer;
}

.key.wide {
	width: 7rem;
}

.key.green {
	background-color: #6aaa64;
}

.key.yellow {
	background-color: #c9b458;
}

.key.absent {
	background-color: #3d4848;
}